'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import VRChatAnalysisForm from '@/components/VRChatAnalysisForm'
import DetectionResultCard from '@/components/DetectionResultCard'
import HistoryList from '@/components/HistoryList'
import AdvancedSettingsDialog from '@/components/AdvancedSettingsDialog'
import VRChatLoginDialog from '@/components/VRChatLoginDialog'

interface DetectionResult {
  type: 'client crash' | 'avatar crash' | 'clean'
  userName: string
  profile: string
  avatarName?: string
  cause?: string
  details?: string
  timestamp: string
  analysisId: string
  isValid: boolean
  profileImage?: string
  avatarImage?: string
  realData?: boolean
}

interface VRChatCredentials {
  username: string
  password: string
  twoFactorCode?: string
}

interface VRChatSession {
  sessionId: string
  user: {
    id: string
    username: string
    displayName: string
    profilePicOverride: string
    userIcon: string
    status: string
    currentAvatarImageUrl: string
    currentAvatarThumbnailImageUrl: string
  }
}

interface HistoryItem {
  id: string
  analysisId: string
  inputId: string
  type: 'client crash' | 'avatar crash' | 'clean'
  userName: string
  avatarName?: string
  cause?: string
  timestamp: string
}

export default function Dashboard() {
  const [currentResult, setCurrentResult] = useState<DetectionResult | null>(null)
  const [history, setHistory] = useState<HistoryItem[]>([])
  const [isAnalyzing, setIsAnalyzing] = useState(false)
  const [isLoginDialogOpen, setIsLoginDialogOpen] = useState(false)
  const [isLoggingIn, setIsLoggingIn] = useState(false)
  const [loginError, setLoginError] = useState('')
  const [vrchatSession, setVrchatSession] = useState<VRChatSession | null>(null)

  // Load history from localStorage on component mount
  useEffect(() => {
    const savedHistory = localStorage.getItem('vrchat-analysis-history')
    if (savedHistory) {
      try {
        const parsed = JSON.parse(savedHistory)
        setHistory(parsed)
      } catch (error) {
        console.error('Error loading history:', error)
      }
    }
  }, [])

  // Save history to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('vrchat-analysis-history', JSON.stringify(history))
  }, [history])

  // Load VRChat session from localStorage
  useEffect(() => {
    const savedSession = localStorage.getItem('vrchat-session')
    if (savedSession) {
      try {
        const session = JSON.parse(savedSession)
        setVrchatSession(session)
      } catch (error) {
        console.error('Error loading VRChat session:', error)
        localStorage.removeItem('vrchat-session')
      }
    }
  }, [])

  const handleVRChatLogin = async (credentials: VRChatCredentials): Promise<boolean> => {
    setIsLoggingIn(true)
    setLoginError('')

    try {
      const response = await fetch('/api/vrchat-auth', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(credentials),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Erreur de connexion')
      }

      if (data.requires2FA) {
        throw new Error('Code d\'authentification à deux facteurs requis')
      }

      if (data.success) {
        const session: VRChatSession = {
          sessionId: data.sessionId,
          user: data.user
        }
        
        setVrchatSession(session)
        localStorage.setItem('vrchat-session', JSON.stringify(session))
        return true
      }

      return false
    } catch (error) {
      setLoginError(error instanceof Error ? error.message : 'Erreur de connexion')
      throw error
    } finally {
      setIsLoggingIn(false)
    }
  }

  const handleVRChatLogout = () => {
    setVrchatSession(null)
    localStorage.removeItem('vrchat-session')
  }

  const handleAnalysisComplete = (result: DetectionResult) => {
    setCurrentResult(result)
    setIsAnalyzing(false)

    // Add to history
    const historyItem: HistoryItem = {
      id: Date.now().toString(),
      analysisId: result.analysisId,
      inputId: extractInputIdFromResult(result),
      type: result.type,
      userName: result.userName,
      avatarName: result.avatarName,
      cause: result.cause,
      timestamp: result.timestamp
    }

    setHistory(prev => [historyItem, ...prev.slice(0, 99)]) // Keep only last 100 items
  }

  const handleAnalysisStart = () => {
    setIsAnalyzing(true)
    setCurrentResult(null)
  }

  const handleSelectHistoryItem = (item: HistoryItem) => {
    // Reconstruct result from history item
    const reconstructedResult: DetectionResult = {
      type: item.type,
      userName: item.userName,
      profile: `https://vrchat.com/home/user/${item.inputId}`,
      avatarName: item.avatarName,
      cause: item.cause,
      details: getDetailsFromType(item.type),
      timestamp: item.timestamp,
      analysisId: item.analysisId,
      isValid: true
    }
    setCurrentResult(reconstructedResult)
  }

  const handleClearHistory = () => {
    setHistory([])
    localStorage.removeItem('vrchat-analysis-history')
  }

  const handleExportToVRCX = () => {
    if (currentResult) {
      // Placeholder for VRCX integration
      const vrcxData = {
        userId: extractInputIdFromResult(currentResult),
        userName: currentResult.userName,
        detectionType: currentResult.type,
        cause: currentResult.cause,
        timestamp: currentResult.timestamp,
        riskLevel: currentResult.type === 'clean' ? 'low' : 'high'
      }
      
      console.log('Export to VRCX:', vrcxData)
      alert('Données exportées vers VRCX (fonctionnalité en développement)')
    }
  }

  const extractInputIdFromResult = (result: DetectionResult): string => {
    // Extract ID from profile URL or use a placeholder
    const match = result.profile.match(/user\/(usr_[a-f0-9-]+)/i)
    return match ? match[1] : 'unknown_id'
  }

  const getDetailsFromType = (type: string): string => {
    switch (type) {
      case 'client crash':
        return 'Client identifié comme potentiellement dangereux avec des modifications suspectes.'
      case 'avatar crash':
        return 'Avatar identifié comme contenant des éléments pouvant causer des crashes.'
      case 'clean':
        return 'Aucune menace détectée - Utilisateur/Avatar sécurisé.'
      default:
        return 'Analyse terminée.'
    }
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header Banner */}
      <div className="w-full h-48 bg-gradient-to-r from-blue-600 to-purple-600 relative overflow-hidden">
        <img 
          src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/42fcc320-7821-47ad-a495-b48f6ae8b643.png" 
          alt="Modern VRChat Crash Detection Dashboard Interface with clean typography, spacious layout and minimal design" 
          className="w-full h-full object-cover opacity-20"
          onError={(e) => {
            const target = e.target as HTMLImageElement
            target.onerror = null
            target.src = ''
          }}
        />
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-center text-white">
            <h1 className="text-4xl font-bold mb-2">VRChat Crash Detection</h1>
            <p className="text-xl opacity-90">Sécurité et Protection de la Plateforme</p>
            <p className="text-sm opacity-75 mt-2">Compatible VRCX • Analyse Avancée • Multi-utilisateurs</p>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="container mx-auto px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Column - Analysis Form and Settings */}
          <div className="space-y-6">
            {/* VRChat Connection Status */}
            {vrchatSession ? (
              <div className="p-4 border rounded-lg bg-green-50 border-green-200">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="font-medium text-green-800">Connecté à VRChat</h3>
                    <p className="text-sm text-green-600">
                      {vrchatSession.user.displayName} (@{vrchatSession.user.username})
                    </p>
                  </div>
                  <div className="flex items-center gap-2">
                    {vrchatSession.user.profilePicOverride && (
                      <img 
                        src={vrchatSession.user.profilePicOverride} 
                        alt="Profile" 
                        className="w-8 h-8 rounded-full"
                      />
                    )}
                    <Button 
                      variant="outline" 
                      size="sm"
                      onClick={handleVRChatLogout}
                    >
                      Déconnecter
                    </Button>
                  </div>
                </div>
              </div>
            ) : (
              <div className="p-4 border rounded-lg bg-yellow-50 border-yellow-200">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="font-medium text-yellow-800">Mode Simulation</h3>
                    <p className="text-sm text-yellow-600">
                      Connectez-vous à VRChat pour des données réelles
                    </p>
                  </div>
                  <Button 
                    variant="outline" 
                    onClick={() => setIsLoginDialogOpen(true)}
                  >
                    Se connecter à VRChat
                  </Button>
                </div>
              </div>
            )}

            <VRChatAnalysisForm 
              onAnalysisComplete={handleAnalysisComplete}
              onAnalysisStart={handleAnalysisStart}
              sessionId={vrchatSession?.sessionId}
            />
            
            <div className="flex gap-2">
              <AdvancedSettingsDialog>
                <Button variant="outline" className="flex-1">
                  Paramètres Avancés
                </Button>
              </AdvancedSettingsDialog>
              
              <Button 
                variant="outline" 
                onClick={() => window.open('https://github.com/vrcx-team/VRCX', '_blank')}
                className="flex-1"
              >
                VRCX Integration
              </Button>
            </div>

            {/* Quick Stats */}
            <div className="grid grid-cols-2 gap-4">
              <div className="p-4 border rounded-lg text-center">
                <div className="text-2xl font-bold text-green-600">{history.filter(h => h.type === 'clean').length}</div>
                <div className="text-sm text-muted-foreground">Sécurisés</div>
              </div>
              <div className="p-4 border rounded-lg text-center">
                <div className="text-2xl font-bold text-red-600">
                  {history.filter(h => h.type === 'client crash' || h.type === 'avatar crash').length}
                </div>
                <div className="text-sm text-muted-foreground">Menaces</div>
              </div>
            </div>
          </div>

          {/* Middle Column - Results */}
          <div className="space-y-6">
            {isAnalyzing && (
              <div className="p-8 border rounded-lg text-center">
                <div className="animate-spin w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full mx-auto mb-4"></div>
                <p className="text-muted-foreground">Analyse en cours...</p>
                <p className="text-sm text-muted-foreground mt-2">Vérification des bases de données de sécurité</p>
              </div>
            )}

            {currentResult && !isAnalyzing && (
              <DetectionResultCard 
                result={currentResult} 
                onExportToVRCX={handleExportToVRCX}
              />
            )}

            {!currentResult && !isAnalyzing && (
              <div className="p-8 border rounded-lg text-center">
                <h3 className="text-lg font-semibold mb-2">Prêt pour l'Analyse</h3>
                <p className="text-muted-foreground">
                  Entrez un ID utilisateur ou avatar pour commencer la détection de sécurité
                </p>
                <div className="mt-4 space-y-2 text-sm text-muted-foreground">
                  <p>✓ Détection de clients crash</p>
                  <p>✓ Analyse d'avatars malveillants</p>
                  <p>✓ Vérification de sécurité avancée</p>
                  <p>✓ Compatible avec VRCX</p>
                </div>
              </div>
            )}
          </div>

          {/* Right Column - History */}
          <div>
            <HistoryList 
              history={history}
              onSelectItem={handleSelectHistoryItem}
              onClearHistory={handleClearHistory}
            />
          </div>
        </div>

        {/* Footer Info */}
        <div className="mt-12 pt-8 border-t text-center text-sm text-muted-foreground">
          <p>VRChat Crash Detection v1.0.0 • Développé pour la sécurité de la communauté VRChat</p>
          <p className="mt-2">
            Compatible avec VRCX • Analyse en temps réel • Protection avancée
          </p>
        </div>
      </div>
    </div>
  )
}
