'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { formatTimestamp, getDetectionStatusColor, getDetectionStatusText } from '@/lib/utils'

interface DetectionResult {
  type: 'client crash' | 'avatar crash' | 'clean'
  userName: string
  profile: string
  avatarName?: string
  cause?: string
  details?: string
  timestamp: string
  analysisId: string
  isValid: boolean
}

interface DetectionResultCardProps {
  result: DetectionResult
  onExportToVRCX?: () => void
}

export default function DetectionResultCard({ result, onExportToVRCX }: DetectionResultCardProps) {
  const statusColor = getDetectionStatusColor(result.type)
  const statusText = getDetectionStatusText(result.type)

  const handleProfileClick = () => {
    window.open(result.profile, '_blank', 'noopener,noreferrer')
  }

  const handleExportToVRCX = () => {
    if (onExportToVRCX) {
      onExportToVRCX()
    } else {
      // Placeholder for VRCX integration
      alert('Fonctionnalité d\'export vers VRCX en cours de développement')
    }
  }

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center gap-2">
            Résultat de l'Analyse
            <Badge className={`${statusColor} border`}>
              {statusText}
            </Badge>
          </CardTitle>
          <div className="text-sm text-muted-foreground">
            {formatTimestamp(result.timestamp)}
          </div>
        </div>
        <CardDescription>
          ID d'analyse: {result.analysisId}
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-6">
        {/* User Information */}
        <div className="space-y-3">
          <h3 className="text-lg font-semibold">Informations Utilisateur</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium text-muted-foreground">Nom d'utilisateur</label>
              <p className="text-base font-medium">{result.userName}</p>
            </div>
            <div>
              <label className="text-sm font-medium text-muted-foreground">Profil VRChat</label>
              <Button 
                variant="link" 
                className="p-0 h-auto text-blue-600 hover:text-blue-800"
                onClick={handleProfileClick}
              >
                Voir le profil
              </Button>
            </div>
          </div>
        </div>

        {/* Avatar Information */}
        {result.avatarName && (
          <div className="space-y-3">
            <h3 className="text-lg font-semibold">Informations Avatar</h3>
            <div>
              <label className="text-sm font-medium text-muted-foreground">Nom de l'avatar</label>
              <p className="text-base font-medium">{result.avatarName}</p>
            </div>
          </div>
        )}

        {/* Detection Results */}
        <div className="space-y-3">
          <h3 className="text-lg font-semibold">Résultats de Détection</h3>
          
          {result.cause && (
            <div className="p-4 rounded-lg border-l-4 border-l-orange-500 bg-orange-50">
              <h4 className="font-medium text-orange-800 mb-2">Cause Identifiée</h4>
              <p className="text-orange-700">{result.cause}</p>
            </div>
          )}

          {result.details && (
            <div className={`p-4 rounded-lg border-l-4 ${
              result.type === 'clean' 
                ? 'border-l-green-500 bg-green-50' 
                : 'border-l-red-500 bg-red-50'
            }`}>
              <h4 className={`font-medium mb-2 ${
                result.type === 'clean' ? 'text-green-800' : 'text-red-800'
              }`}>
                Détails de l'Analyse
              </h4>
              <p className={result.type === 'clean' ? 'text-green-700' : 'text-red-700'}>
                {result.details}
              </p>
            </div>
          )}
        </div>

        {/* Action Buttons */}
        <div className="flex gap-3 pt-4 border-t">
          <Button 
            onClick={handleExportToVRCX}
            variant="outline"
            className="flex-1"
          >
            Exporter vers VRCX
          </Button>
          
          <Button 
            onClick={() => navigator.clipboard.writeText(JSON.stringify(result, null, 2))}
            variant="outline"
            className="flex-1"
          >
            Copier les Résultats
          </Button>
        </div>

        {/* Warning for crash detection */}
        {(result.type === 'client crash' || result.type === 'avatar crash') && (
          <div className="p-4 rounded-lg bg-yellow-50 border border-yellow-200">
            <h4 className="font-medium text-yellow-800 mb-2">⚠️ Avertissement de Sécurité</h4>
            <p className="text-yellow-700 text-sm">
              Cet utilisateur/avatar a été identifié comme potentiellement dangereux. 
              Recommandations: Évitez les interactions directes et signalez si nécessaire aux modérateurs VRChat.
            </p>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
