'use client'

import { useState, useEffect } from 'react'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Label } from '@/components/ui/label'
import { Slider } from '@/components/ui/slider'
import { Switch } from '@/components/ui/switch'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'

interface AdvancedSettings {
  detectionSensitivity: number
  verificationThreshold: number
  enableRealTimeAnalysis: boolean
  enableHistoryLogging: boolean
  enableVRCXIntegration: boolean
  analysisDepth: 'basic' | 'standard' | 'advanced' | 'deep'
  autoRefreshInterval: number
  maxHistoryItems: number
  enableNotifications: boolean
  notificationLevel: 'critical' | 'warning' | 'all'
}

const defaultSettings: AdvancedSettings = {
  detectionSensitivity: 75,
  verificationThreshold: 60,
  enableRealTimeAnalysis: false,
  enableHistoryLogging: true,
  enableVRCXIntegration: true,
  analysisDepth: 'standard',
  autoRefreshInterval: 30,
  maxHistoryItems: 100,
  enableNotifications: true,
  notificationLevel: 'warning'
}

interface AdvancedSettingsDialogProps {
  children: React.ReactNode
  onSettingsChange?: (settings: AdvancedSettings) => void
}

export default function AdvancedSettingsDialog({ children, onSettingsChange }: AdvancedSettingsDialogProps) {
  const [settings, setSettings] = useState<AdvancedSettings>(defaultSettings)
  const [isOpen, setIsOpen] = useState(false)

  // Load settings from localStorage on component mount
  useEffect(() => {
    const savedSettings = localStorage.getItem('vrchat-detection-settings')
    if (savedSettings) {
      try {
        const parsed = JSON.parse(savedSettings)
        setSettings({ ...defaultSettings, ...parsed })
      } catch (error) {
        console.error('Error loading settings:', error)
      }
    }
  }, [])

  const handleSettingChange = (key: keyof AdvancedSettings, value: any) => {
    const newSettings = { ...settings, [key]: value }
    setSettings(newSettings)
    
    // Save to localStorage
    localStorage.setItem('vrchat-detection-settings', JSON.stringify(newSettings))
    
    // Notify parent component
    if (onSettingsChange) {
      onSettingsChange(newSettings)
    }
  }

  const handleResetSettings = () => {
    setSettings(defaultSettings)
    localStorage.setItem('vrchat-detection-settings', JSON.stringify(defaultSettings))
    if (onSettingsChange) {
      onSettingsChange(defaultSettings)
    }
  }

  const handleExportSettings = () => {
    const dataStr = JSON.stringify(settings, null, 2)
    const dataBlob = new Blob([dataStr], { type: 'application/json' })
    const url = URL.createObjectURL(dataBlob)
    const link = document.createElement('a')
    link.href = url
    link.download = 'vrchat-detection-settings.json'
    link.click()
    URL.revokeObjectURL(url)
  }

  const handleImportSettings = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        try {
          const imported = JSON.parse(e.target?.result as string)
          const newSettings = { ...defaultSettings, ...imported }
          setSettings(newSettings)
          localStorage.setItem('vrchat-detection-settings', JSON.stringify(newSettings))
          if (onSettingsChange) {
            onSettingsChange(newSettings)
          }
        } catch (error) {
          alert('Erreur lors de l\'importation des paramètres')
        }
      }
      reader.readAsText(file)
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        {children}
      </DialogTrigger>
      <DialogContent className="max-w-4xl max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Paramètres Avancés</DialogTitle>
          <DialogDescription>
            Configurez les paramètres de détection et d'analyse pour optimiser les performances
          </DialogDescription>
        </DialogHeader>

        <Tabs defaultValue="detection" className="w-full">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="detection">Détection</TabsTrigger>
            <TabsTrigger value="analysis">Analyse</TabsTrigger>
            <TabsTrigger value="integration">Intégration</TabsTrigger>
            <TabsTrigger value="advanced">Avancé</TabsTrigger>
          </TabsList>

          <TabsContent value="detection" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Paramètres de Détection</CardTitle>
                <CardDescription>
                  Ajustez la sensibilité et les seuils de détection
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="space-y-2">
                  <Label>Sensibilité de Détection: {settings.detectionSensitivity}%</Label>
                  <Slider
                    value={[settings.detectionSensitivity]}
                    onValueChange={(value) => handleSettingChange('detectionSensitivity', value[0])}
                    max={100}
                    min={0}
                    step={5}
                    className="w-full"
                  />
                  <p className="text-xs text-muted-foreground">
                    Plus élevé = plus sensible aux menaces potentielles
                  </p>
                </div>

                <div className="space-y-2">
                  <Label>Seuil de Vérification: {settings.verificationThreshold}%</Label>
                  <Slider
                    value={[settings.verificationThreshold]}
                    onValueChange={(value) => handleSettingChange('verificationThreshold', value[0])}
                    max={100}
                    min={0}
                    step={5}
                    className="w-full"
                  />
                  <p className="text-xs text-muted-foreground">
                    Seuil minimum pour considérer une détection comme valide
                  </p>
                </div>

                <div className="space-y-2">
                  <Label>Profondeur d'Analyse</Label>
                  <Select 
                    value={settings.analysisDepth} 
                    onValueChange={(value) => handleSettingChange('analysisDepth', value)}
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="basic">Basique (Rapide)</SelectItem>
                      <SelectItem value="standard">Standard (Recommandé)</SelectItem>
                      <SelectItem value="advanced">Avancé (Précis)</SelectItem>
                      <SelectItem value="deep">Approfondi (Très précis)</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="analysis" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Paramètres d'Analyse</CardTitle>
                <CardDescription>
                  Configuration de l'analyse en temps réel et de l'historique
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="flex items-center justify-between">
                  <div className="space-y-0.5">
                    <Label>Analyse en Temps Réel</Label>
                    <p className="text-xs text-muted-foreground">
                      Analyse automatique des nouveaux utilisateurs détectés
                    </p>
                  </div>
                  <Switch
                    checked={settings.enableRealTimeAnalysis}
                    onCheckedChange={(checked) => handleSettingChange('enableRealTimeAnalysis', checked)}
                  />
                </div>

                <div className="flex items-center justify-between">
                  <div className="space-y-0.5">
                    <Label>Enregistrement de l'Historique</Label>
                    <p className="text-xs text-muted-foreground">
                      Sauvegarder l'historique des analyses
                    </p>
                  </div>
                  <Switch
                    checked={settings.enableHistoryLogging}
                    onCheckedChange={(checked) => handleSettingChange('enableHistoryLogging', checked)}
                  />
                </div>

                <div className="space-y-2">
                  <Label>Intervalle de Rafraîchissement: {settings.autoRefreshInterval}s</Label>
                  <Slider
                    value={[settings.autoRefreshInterval]}
                    onValueChange={(value) => handleSettingChange('autoRefreshInterval', value[0])}
                    max={300}
                    min={5}
                    step={5}
                    className="w-full"
                  />
                </div>

                <div className="space-y-2">
                  <Label>Nombre Maximum d'Éléments dans l'Historique: {settings.maxHistoryItems}</Label>
                  <Slider
                    value={[settings.maxHistoryItems]}
                    onValueChange={(value) => handleSettingChange('maxHistoryItems', value[0])}
                    max={1000}
                    min={10}
                    step={10}
                    className="w-full"
                  />
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="integration" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Intégration VRCX</CardTitle>
                <CardDescription>
                  Configuration de l'intégration avec VRCX pour VRChat
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="flex items-center justify-between">
                  <div className="space-y-0.5">
                    <Label>Activer l'Intégration VRCX</Label>
                    <p className="text-xs text-muted-foreground">
                      Permet l'export et la synchronisation avec VRCX
                    </p>
                  </div>
                  <Switch
                    checked={settings.enableVRCXIntegration}
                    onCheckedChange={(checked) => handleSettingChange('enableVRCXIntegration', checked)}
                  />
                </div>

                <div className="flex items-center justify-between">
                  <div className="space-y-0.5">
                    <Label>Notifications</Label>
                    <p className="text-xs text-muted-foreground">
                      Recevoir des notifications pour les détections
                    </p>
                  </div>
                  <Switch
                    checked={settings.enableNotifications}
                    onCheckedChange={(checked) => handleSettingChange('enableNotifications', checked)}
                  />
                </div>

                {settings.enableNotifications && (
                  <div className="space-y-2">
                    <Label>Niveau de Notification</Label>
                    <Select 
                      value={settings.notificationLevel} 
                      onValueChange={(value) => handleSettingChange('notificationLevel', value)}
                    >
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="critical">Critique uniquement</SelectItem>
                        <SelectItem value="warning">Avertissements et critique</SelectItem>
                        <SelectItem value="all">Toutes les détections</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="advanced" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Paramètres Avancés</CardTitle>
                <CardDescription>
                  Gestion des paramètres et options avancées
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="grid grid-cols-2 gap-4">
                  <Button onClick={handleExportSettings} variant="outline">
                    Exporter les Paramètres
                  </Button>
                  
                  <div>
                    <input
                      type="file"
                      accept=".json"
                      onChange={handleImportSettings}
                      style={{ display: 'none' }}
                      id="import-settings"
                    />
                    <Button 
                      onClick={() => document.getElementById('import-settings')?.click()}
                      variant="outline"
                      className="w-full"
                    >
                      Importer les Paramètres
                    </Button>
                  </div>
                </div>

                <Button 
                  onClick={handleResetSettings} 
                  variant="destructive" 
                  className="w-full"
                >
                  Réinitialiser aux Valeurs par Défaut
                </Button>

                <div className="pt-4 border-t">
                  <h4 className="font-medium mb-2">Informations Système</h4>
                  <div className="text-xs text-muted-foreground space-y-1">
                    <p>Version: 1.0.0</p>
                    <p>Dernière mise à jour: {new Date().toLocaleDateString('fr-FR')}</p>
                    <p>Paramètres sauvegardés: {localStorage.getItem('vrchat-detection-settings') ? 'Oui' : 'Non'}</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>

        <div className="flex justify-end gap-2 pt-4 border-t">
          <Button variant="outline" onClick={() => setIsOpen(false)}>
            Annuler
          </Button>
          <Button onClick={() => setIsOpen(false)}>
            Sauvegarder
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}
