'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { isValidVRChatId } from '@/lib/utils'

interface AnalysisResult {
  type: 'client crash' | 'avatar crash' | 'clean'
  userName: string
  profile: string
  avatarName?: string
  cause?: string
  details?: string
  timestamp: string
  analysisId: string
  isValid: boolean
}

interface VRChatAnalysisFormProps {
  onAnalysisComplete: (result: AnalysisResult) => void
  onAnalysisStart: () => void
  sessionId?: string
}

export default function VRChatAnalysisForm({ onAnalysisComplete, onAnalysisStart, sessionId }: VRChatAnalysisFormProps) {
  const [inputId, setInputId] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!inputId.trim()) {
      setError('Veuillez entrer un ID utilisateur ou avatar')
      return
    }

    if (!isValidVRChatId(inputId.trim())) {
      setError('Format d\'ID invalide. Utilisez usr_... pour les utilisateurs ou avtr_... pour les avatars')
      return
    }

    setError('')
    setIsLoading(true)
    onAnalysisStart()

    try {
      const response = await fetch('/api/detect', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ 
          id: inputId.trim(),
          sessionId: sessionId 
        }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Erreur lors de l\'analyse')
      }

      onAnalysisComplete(data)
      setInputId('')
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Erreur lors de l\'analyse')
    } finally {
      setIsLoading(false)
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputId(e.target.value)
    if (error) setError('')
  }

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle>Analyse de Sécurité VRChat</CardTitle>
        <CardDescription>
          Entrez un ID utilisateur (usr_...) ou un ID d'avatar (avtr_...) pour détecter les clients ou avatars crash
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Input
              type="text"
              placeholder="Entrez l'ID utilisateur ou d'avatar (ex: usr_dc9e25b5-0622-446b-92ea-f24815598d6b)"
              value={inputId}
              onChange={handleInputChange}
              disabled={isLoading}
              className={error ? 'border-red-500 focus:border-red-500' : ''}
            />
            {error && (
              <p className="text-sm text-red-600">{error}</p>
            )}
          </div>
          
          <div className="flex gap-2">
            <Button 
              type="submit" 
              disabled={isLoading || !inputId.trim()}
              className="flex-1"
            >
              {isLoading ? 'Analyse en cours...' : 'Analyser'}
            </Button>
            
            <Button 
              type="button" 
              variant="outline"
              onClick={() => {
                setInputId('')
                setError('')
              }}
              disabled={isLoading}
            >
              Effacer
            </Button>
          </div>
        </form>

        <div className="mt-6 space-y-2">
          <h4 className="text-sm font-medium text-muted-foreground">Exemples d'ID:</h4>
          <div className="space-y-1 text-xs text-muted-foreground">
            <div>
              <strong>Utilisateur:</strong> usr_dc9e25b5-0622-446b-92ea-f24815598d6b
            </div>
            <div>
              <strong>Avatar:</strong> avtr_4bce3511-8436-4e82-b32f-c88994519e51
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
