import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// VRChat ID validation utilities
export function isValidUserId(id: string): boolean {
  return id.startsWith('usr_') && id.length === 40
}

export function isValidAvatarId(id: string): boolean {
  return id.startsWith('avtr_') && id.length === 41
}

export function isValidVRChatId(id: string): boolean {
  return isValidUserId(id) || isValidAvatarId(id)
}

// Date formatting utilities
export function formatTimestamp(date: Date | string): string {
  const dateObj = typeof date === 'string' ? new Date(date) : date
  return new Intl.DateTimeFormat('fr-FR', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit'
  }).format(dateObj)
}

export function formatRelativeTime(date: Date | string): string {
  const dateObj = typeof date === 'string' ? new Date(date) : date
  const now = new Date()
  const diffInSeconds = Math.floor((now.getTime() - dateObj.getTime()) / 1000)
  
  if (diffInSeconds < 60) {
    return 'Il y a quelques secondes'
  } else if (diffInSeconds < 3600) {
    const minutes = Math.floor(diffInSeconds / 60)
    return `Il y a ${minutes} minute${minutes > 1 ? 's' : ''}`
  } else if (diffInSeconds < 86400) {
    const hours = Math.floor(diffInSeconds / 3600)
    return `Il y a ${hours} heure${hours > 1 ? 's' : ''}`
  } else {
    const days = Math.floor(diffInSeconds / 86400)
    return `Il y a ${days} jour${days > 1 ? 's' : ''}`
  }
}

// Detection result utilities
export function getDetectionStatusColor(type: string): string {
  switch (type) {
    case 'client crash':
      return 'text-red-600 bg-red-50 border-red-200'
    case 'avatar crash':
      return 'text-orange-600 bg-orange-50 border-orange-200'
    case 'clean':
      return 'text-green-600 bg-green-50 border-green-200'
    default:
      return 'text-gray-600 bg-gray-50 border-gray-200'
  }
}

export function getDetectionStatusText(type: string): string {
  switch (type) {
    case 'client crash':
      return 'Client Crash Détecté'
    case 'avatar crash':
      return 'Avatar Crash Détecté'
    case 'clean':
      return 'Sécurisé'
    default:
      return 'Inconnu'
  }
}
