'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { ScrollArea } from '@/components/ui/scroll-area'
import { formatRelativeTime, getDetectionStatusColor, getDetectionStatusText } from '@/lib/utils'

interface HistoryItem {
  id: string
  analysisId: string
  inputId: string
  type: 'client crash' | 'avatar crash' | 'clean'
  userName: string
  avatarName?: string
  cause?: string
  timestamp: string
}

interface HistoryListProps {
  history: HistoryItem[]
  onSelectItem: (item: HistoryItem) => void
  onClearHistory: () => void
}

export default function HistoryList({ history, onSelectItem, onClearHistory }: HistoryListProps) {
  if (history.length === 0) {
    return (
      <Card className="w-full h-full">
        <CardHeader>
          <CardTitle>Historique des Analyses</CardTitle>
          <CardDescription>
            Aucune analyse effectuée pour le moment
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="text-center py-8 text-muted-foreground">
            <p>Commencez par analyser un ID utilisateur ou avatar</p>
            <p className="text-sm mt-2">L'historique apparaîtra ici</p>
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card className="w-full h-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <div>
            <CardTitle>Historique des Analyses</CardTitle>
            <CardDescription>
              {history.length} analyse{history.length > 1 ? 's' : ''} effectuée{history.length > 1 ? 's' : ''}
            </CardDescription>
          </div>
          <Button 
            variant="outline" 
            size="sm"
            onClick={onClearHistory}
          >
            Effacer
          </Button>
        </div>
      </CardHeader>
      
      <CardContent className="p-0">
        <ScrollArea className="h-[400px] px-6 pb-6">
          <div className="space-y-3">
            {history.map((item) => (
              <HistoryItemCard 
                key={item.analysisId} 
                item={item} 
                onSelect={() => onSelectItem(item)}
              />
            ))}
          </div>
        </ScrollArea>
      </CardContent>
    </Card>
  )
}

interface HistoryItemCardProps {
  item: HistoryItem
  onSelect: () => void
}

function HistoryItemCard({ item, onSelect }: HistoryItemCardProps) {
  const statusColor = getDetectionStatusColor(item.type)
  const statusText = getDetectionStatusText(item.type)
  
  const isUserAnalysis = item.inputId.startsWith('usr_')
  const analysisType = isUserAnalysis ? 'Utilisateur' : 'Avatar'

  return (
    <div 
      className="p-4 border rounded-lg hover:bg-muted/50 cursor-pointer transition-colors"
      onClick={onSelect}
    >
      <div className="flex items-start justify-between mb-2">
        <div className="flex items-center gap-2">
          <Badge variant="outline" className="text-xs">
            {analysisType}
          </Badge>
          <Badge className={`${statusColor} border text-xs`}>
            {statusText}
          </Badge>
        </div>
        <span className="text-xs text-muted-foreground">
          {formatRelativeTime(item.timestamp)}
        </span>
      </div>
      
      <div className="space-y-1">
        <p className="font-medium text-sm truncate">
          {item.userName}
        </p>
        
        {item.avatarName && (
          <p className="text-xs text-muted-foreground truncate">
            Avatar: {item.avatarName}
          </p>
        )}
        
        <p className="text-xs text-muted-foreground truncate">
          ID: {item.inputId}
        </p>
        
        {item.cause && (
          <p className="text-xs text-orange-600 truncate">
            {item.cause}
          </p>
        )}
      </div>
    </div>
  )
}
